
.picHolder {
  box-shadow: 0px 10px 25px 15px rgba(0, 0, 0, 0.15);
  border-radius: 8%;
}

.logo {
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  z-index: 2;
}

.navigation ul {
  display: flex;
  flex-direction: row;

  justify-content: left;
  align-items: center;
  text-align: center;
}

.navigation li {
  width: auto;
  padding: 1rem;
  margin-left: 5%;
  border-radius: 5% 5% 45% 45%;
  z-index: 0;
}

@keyframes buttonHover {
  from {
    background-color: white;
    font-size: medium;
  }
  to {
    background-color: rgb(200, 16, 43);
    box-shadow: 0 4px 9px 2px rgba(0, 0, 0, 0.45);
    color: white;
    transform: scale(1.15);
  }
}
.navigation li:hover {
  animation-name: buttonHover;
  animation-duration: 0.1s;
  animation-fill-mode: forwards;
}
.navigation li.active:hover {
  animation-name: none;
}

.navigation a {
  color: var(--dark-grey-blue);
}

.navigation li.active {
  background-color: rgb(200, 16, 43);
  box-shadow: 0 4px 9px 2px rgba(0, 0, 0, 0.45);
  color: white;
  transform: scale(1.15);
  z-index: 1;
}

.cart {
  position: absolute;
  right: 5vw;
  margin-top: 2rem;
  cursor: pointer;
}

.cart.invisible {
  position: fixed;
  top: -2rem;
  right: 1vw;
  cursor: pointer;
  background-color: rgb(200, 16, 43);
  box-shadow: 0 4px 9px 2px rgba(0, 0, 0, 0.45);
  color: white;
  z-index: 50;
}

.cart.invisible:hover {
  animation-name: none;
}

.cart.invisible .tooltip {
  visibility: visible;
}
.cart.invisible .cartitems {
  visibility: visible;
  left: 6.5rem;
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.25);
}

.tooltip {
  visibility: hidden;
  width: auto;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-size: small;
}

.cart:hover .tooltip {
  visibility: visible;
}

.cartitems {
  visibility: visible;
  width: auto;
  background-color: rgb(200, 16, 43);
  color: #fff;
  text-align: center;
  padding: 0.2rem 0.4rem;
  border-radius: 50%;
  position: absolute;
  top: -0rem;
  left: 6rem;
  z-index: 1;
}

.cart:hover .cartitems {
  visibility: visible;
  left: 6.5rem;
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.25);
}

@media only screen and (max-width: 900px) {
  .cart.invisible {
    position: fixed;
    top: -3.2rem;
    right: -1.5rem;
    cursor: pointer;
    background-color: rgb(200, 16, 43);
    box-shadow: 0 4px 9px 2px rgba(0, 0, 0, 0.45);
    color: white;
    transform: scale(0.5);
  }

  .tooltip {
    visibility: hidden;
    width: auto;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    font-size: small;
  }
}
